import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { loginPost } from '@api/memberApi';
import { getCookie, removeCookie, setCookie } from '@utils/cookieUtil';
import { Member, MemberStatus } from '@typings/db';
import { postCreateWorkspaceAsync } from '@slices/workspaceSlice';

const initState: Member = {
  id: 0,
  email: '',
  name: '',
  nickname: '',
  password: '',
  ownedWorkspaces: [],
  workspaces: [],
  memberRoleList: [],
  memberStatus: MemberStatus.INACTIVE,
  createdAt: '',
  modifiedAt: '',
};

export const loginPostAsync = createAsyncThunk('loginPostAsync', (param: { email: string, password: string }) => {
  return loginPost(param);
});

const loadMemberCookie = (): Member | undefined => {
  let memberInfo: Member = initState;
  //    
  const memberCookieValue = getCookie('member');
  if (memberCookieValue) {
    const parsedMemberCookieValue = JSON.parse(memberCookieValue);
    memberInfo.nickname = decodeURIComponent(parsedMemberCookieValue.nickname);
    return parsedMemberCookieValue as Member;
  }
  return undefined;
};

const memberSlice = createSlice({
  name: 'memberSlice',
  initialState: loadMemberCookie() || initState, //    
  reducers: {
    login: (state, action: PayloadAction<Member>) => {
      setCookie('member', JSON.stringify(action.payload), 1); // 1   
      return action.payload;
    },
    logout: () => {
      console.log('logout...');
      removeCookie('member');
      return { ...initState };
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(loginPostAsync.fulfilled, (state, action: PayloadAction<Member>) => {
        console.log('fulfilled');
        const payload = action.payload;
        if (!('error' in payload)) {
          setCookie('member', JSON.stringify(payload), 1); // 1   
        } else {
          console.log('Login failed:', payload.error);
        }
        return payload;
      })
      // postCreateWorkspaceAsync      
      .addCase(postCreateWorkspaceAsync.fulfilled, (state, action: PayloadAction<{ member: Member }>) => {
        state.ownedWorkspaces = action.payload.member.ownedWorkspaces; //   
        state.workspaces = action.payload.member.workspaces; //   
      })
      .addCase(loginPostAsync.pending, () => {
        console.log('pending');
      })
      .addCase(loginPostAsync.rejected, () => {
        console.log('rejected');
      });
  },
});

export const { login, logout } = memberSlice.actions;

export default memberSlice.reducer;
